<template>
  <div class="home">
    <div class="header">
      <h1><span class="title">超级管理系统</span></h1>
    </div>
    <div class="left">
      <el-row class="tac">
        <el-col>
          <el-menu default-active="1" class="el-menu-vertical-demo" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
            <el-menu-item @click="teacherManager" index="1">
              <span>教师管理</span>
            </el-menu-item>
            <el-menu-item @click="studentManager" index="2">
              <span>学生管理</span>
            </el-menu-item>
            <el-menu-item @click="courseManager" index="3">
              <span>课程管理</span>
            </el-menu-item>
          </el-menu>
        </el-col>
      </el-row>
    </div>
    <!-- 主内容区域 -->
    <div class="right">
      <!-- 内容顶栏 -->
      <div style="width:100%;" class="right-top">
        <div style="float: right;">
          <el-input style="width:30%;" size="small"></el-input>
          <el-button type="warning" icon="el-icon-search" size="small">搜索</el-button>
          <el-button type="success" @click="openDialog('add','')" size="small">增加</el-button>
        </div>
      </div>
      <!-- 数据显示 -->
      <el-table ref="multipleTable" :data="tabledata" height=500 width=100 style="width: 100%">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column v-for="(name, index) in colunm_name" :prop="name" :key='index' :label="name" style="width:20%">
        </el-table-column>
        <el-table-column width="200" style="width: 50%">
          fixed="right"
          label="Operations"
          style="width:50%“>
          <template slot-scope="scope">
            <el-button size="mini" type="warning" @click="openDialog('update',scope.$index)">更新</el-button>
            <el-button size="mini" type="danger" @click="del(scope.$index)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 对话框 -->
      <el-dialog title="增加一条新的数据" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item v-for="(name, index) in colunm_name" :prop="name" :key='index' :label="name" :label-width="formLabelWidth">
            <el-input v-model="form[name]" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取消</el-button>
          <el-button type="primary" @click="dialog_ok()">确定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import qs from 'qs'

export default {
  name: 'Home',
  teachers_list: {},
  data() {
    return {
      tabledata: [],
      colunm_name: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      request: {
        url: ''
      },
      form: {

      },
      fn: '',
      index: '',
      formLabelWidth: '120px',
      user: {
        name: '登陆'
      },
      col_name_data:{
        data_name:'teacher'
      }
    }
  },
  methods: {
    get_col_name(){
      this.api('get_column_name/', 'post', self.col_name_data, 'col')
    },
    teacherManager() {
      self = this;
      self.request.url = 'teachers/'
      self.col_name_data.data_name='teacher'
      this.get_col_name()
      this.api('teachers/', 'get', {}, 'list');
    },
    studentManager() {
      self = this;
      self.request.url = 'students/'
      self.col_name_data.data_name='student'
      this.get_col_name()
      this.api('students/', 'get', {}, 'list');
    },
    courseManager() {
      self = this;
      self.request.url = 'courses/'
      self.col_name_data.data_name='course'
      this.get_col_name()
      this.api('courses/', 'get', {}, 'list');
      // this.login()
    },
    openDialog(fn, index) {
      self = this;
      self.fn = fn;
      if(fn=='update'){
        self.index = index;
        self.form = self.tabledata[index]
        console.log(self.tabledata)
      }
      self.dialogFormVisible = true;
    },
    api(url, method, data, fn) {
      self = this
      // 为给定 ID 的 user 创建请求
      axios({
          method: method,
          url: 'http://47.100.211.7:8000/api/' + url,
          data: qs.stringify(data),
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          }

        })
        .then(function(response) {
          if(fn=='col'){
            self.colunm_name = response.data["column_name"]
          }
          if (fn == 'list') {

            self.tabledata = response.data;
            if(self.tabledata[0]){
              self.colunm_name = Object.keys(self.tabledata[0]);

            }
            // for (let key in Object.keys(self.tabledata[0])) {
            //   if (self.colunm_name[key]) {
            //     // self.form[self.colunm_name[key]] = ""
            //     //注册动态响应属性
            //     self.$set(self.form, self.colunm_name[key], "")

            //   }
            // }
          } else if (fn == 'add') {
            self.tabledata.push(response.data)
          } else if (fn == 'del') {

          } else if (fn == 'update') {

          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    dialog_ok() {
      self = this;
      let fn = self.fn
      self.dialogFormVisible = false;

      if (fn == 'add') {
        this.api(self.request.url, 'post', self.form, fn)
      }
      if (self.fn == 'update') {
        let id = self.tabledata[self.index]['id']
        this.api(self.request.url + id + '/', 'put', self.form, 'update')
      }
      this.api(self.request.url, 'get', {}, '')

    },
    del(index) {
      let id = self.tabledata[index]['id']
      this.api(self.request.url + id + '/', 'delete', self.form, 'del')
      console.log(self.tabledata)
      self.tabledata.splice(index,1);
      this.api(self.request.url, 'get', {}, 'del')
      console.log(self.tabledata)
    },


  }
}

</script>
<style>
.header {
  background: #181C1F;
  color: #F5F4E8;
  margin: 0;
  padding: 0;
}

h1 {
  background: #181C1F;
  color: #F5F4E8;
  margin: 0;
  padding: 0;
  line-height: 64px;
  display: inline;
}

.title {
  margin-left: 30px;
}


#app {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.home {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  height: 100%;
  width: 100%;

}

.left div {
  height: 100%;
}

el-row: {
  width: 100%;
  bottom: 0;
}

.left span {
  width: 100%;
  text-align: center;
}

.left {
  float: left;
  position: absolute;
  left: 0;
  width: 30%;
  /*height:100%;*/
  top: 64px;
  bottom: 0;
}

.right {
  position: absolute;
  right: 0;
  width: 70%;
}

el-tabel {
  width: 100%;
}


.right-top {
  margin-top: 14px;
}

</style>



